.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: darkblue;
  color: orange;
  font-family: "Rubik", sans-serif;
}
.App .clock-title {
  font-size: 3rem;
  padding: 30px;
}
.App .settings-container {
  display: flex;
  width: 100%;
  height: auto;
}
.App .break-settings-container,
.App .session-settings-container {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.App .break-settings-container .label,
.App .session-settings-container .label {
  font-size: 3rem;
}
.App .increment-decrement-container {
  display: flex;
  padding: 20px;
  align-items: center;
}
.App .increment-decrement-container .fa-arrow-down,
.App .increment-decrement-container .fa-arrow-up {
  position: relative;
  top: 0;
  transition: top ease 0.3s;
  cursor: pointer;
  margin: 0 20px;
  font-size: 3rem;
}
.App .increment-decrement-container .fa-arrow-down:hover {
  top: 7px;
}
.App .increment-decrement-container .fa-arrow-up:hover {
  top: -7px;
}
.App .increment-decrement-container .length {
  font-size: 4rem;
  width: 100px;
  text-align: center;
}
.App .timer-container {
  text-align: center;
}
.App .timer-container .label {
  font-size: 3.5rem;
}
.App .timer-container .time-left {
  font-size: 3rem;
}
.App .timer-container .controls-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.App .timer-container .controls-container i {
  font-size: 4rem;
  padding: 20px;
}

